.popup{
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100vh;
    display: flex;
    justify-content: center;
    align-items: center;
}

.popup-inner{
    position: relative;
    padding: 32px;
    width: 100%;
    max-width: 640px;
    background-color: rgb(120, 235, 235);

}

.popup-inner.closebtn{
    position: absolute;
    top: 16px;
    right: 16px;
}